/*=====================
    3.Home slider CSS start
==========================*/
.home-slider,
.center-slider {
  .slick-prev,
  .slick-next {
    opacity: 0;
    transform: scale(1);
    transition: all 0.5s ease;
    margin-top: -10px;

    &:before {
      font: normal normal normal 14px/1 $font-awesome;
      opacity: 1;
      color: black;
      background-color: rgba($white, 0.5);
      border-radius: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .slick-next {
    right: 1px;

    &:before {
      content: "\f105";
    }
  }

  .slick-prev {
    left: 1px;
    z-index: 1;

    &:before {
      content: "\f104";
    }
  }

  .home {
    background-size: cover !important;
    background-repeat: no-repeat !important;
    background-position: right !important;
    height: 75vh !important;

    @for $i from 1 through 56 {
      &.home#{$i} {
        // background-image:url("#{$assetspath}/home-banner/#{$i}.jpg");
        background-image: url("#{$assetspath}/vegetables/theme/#{$i}.jpeg");
      }
    }
  }

  .container {
    min-width: 90vw !important;
    max-width: 90vw !important;
  }

  .container-navbar {
    max-width: 90% !important;
    max-height: 10vh;
    margin-left: auto;
    margin-right: auto;
  }

  .slider-contain {
    width: 100%;
    height: 75vh;
    display: flex;
    align-items: center;

