/*=====================
    24.RTL CSS start
==========================*/
body.rtl {
  direction: rtl;
}

.rtl {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    text-align: right;
  }

  .slick-slider {
    direction: ltr;
  }

  .slick-slide {
    float: left;
  }

  ul {
    -webkit-padding-start: 0;
  }

  .mr-3 {
    margin-right: 0 !important;
    margin-left: 5px;
  }

  .offset-md-3,
  .offset-lg-2,
  .offset-xl-3,
  .offset-lg-3,
  .offset-md-2 {
    margin: 0 auto;
  }

  .title1,
  .title2,
  .title3,
  .title4 {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
